.wrpBalance {
  width: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.balance {
  max-width: 200px;
}

.coinStatic {
  width: 25px;
  height: 25px;
  margin-left: 10px;
}
.coinStatic:hover {
  animation: myAnimShake 2s ease 0s 1 normal forwards;
}

@keyframes myAnimShake {
  0%,
  100% {
    transform: rotate(0deg);
    transform-origin: 50% 0;
  }

  10% {
    transform: rotate(2deg);
  }

  20%,
  40%,
  60% {
    transform: rotate(-10deg);
  }

  30%,
  50%,
  70% {
    transform: rotate(4deg);
  }

  80% {
    transform: rotate(-2deg);
  }

  90% {
    transform: rotate(2deg);
  }
}
